@import '../../lib/sass/px2rem';
@import '../../lib/sass/mixin';

@keyframes fadeInUp {
    100% {
        bottom: 0
    }
}
.selectBox { width: 100%; min-height: q(90); display: flex; justify-content: space-between; align-items: center; position: relative;
    .leftText { width: 50%; line-height: q(34); font-size: q(26); color: #666;}
    .rightBox { display: flex; justify-content: space-between; text-align: right; align-items: center; flex: 0 1 50%;}
    .selectInput { display: block; font-size: q(28); line-height: q(36); outline: none; margin-left: auto; text-align: left; @include extend-click(); color: #c1c0c0;}
    .selectInput.light { color: #666;}
    .selectIcon { width: q(16); height: q(27); background: url('../images/selectIcon.png') center/q(16) q(27) no-repeat; margin-left: q(90);}
}
.select_box { display: none; position: fixed; left: 0; right: 0; top: 0; bottom: 0; background-color: rgba(0, 0, 0, .26); transition: all .5s; z-index: 2;
    .select { height: q(500); box-sizing: border-box; overflow: auto; text-align: center; padding: q(90) q(24) q(20); position: absolute; left: 0; right: 0; bottom: -100%; background-color: #fff;
        .top { display: flex; justify-content: space-between; position: absolute; height: q(90); top: 0; width: 90%; left: 50%; transform: translateX(-50%); align-items: center;}
        .selectData { height: q(390);
            .swiper-container { height: q(390);}
            .swiper-slide { font-size: q(26); line-height: q(97.5); color: #666;}
            .swiper-slide-prev,
            .swiper-slide-next { color: #666;}
            .swiper-slide-active { color: #3aa5ff;}
        }
    }
}
.select_box {
    .select { animation: fadeInUp .3s ease-out; animation-fill-mode: forwards;
        .close { color: #808080; font-size: q(28);}
        .ok { color: #3aa5ff; font-size: q(28);}
        .title { font-size: q(36); color: #666;}
        .close,
        .ok { @include extend-click();}
    }
}

.addSelect_box { position: fixed; left: 0; right: 0; top: 0; bottom: 0; background-color: rgba(0, 0, 0, .26); transition: all .5s; z-index: 2;}
.addSelect_box .select { height: q(400); box-sizing: border-box; overflow: auto; text-align: center; padding-left: 0; padding-right: 0; background-color: #f5f5f5; position: absolute; left: 0; right: 0; bottom: -100%; animation: fadeInUp .3s ease-out; animation-fill-mode: forwards;
    .top { display: flex; justify-content: space-between; height: q(90); width: 100%; background-color: #fff; padding: 0 5%; align-items: center;}
    .selectData { display: flex; justify-content: space-between; height: 100%; position: relative;
        &:before,
        &:after { content: ''; display: block; position: absolute; width: 92%; height: 1px; border-top: 1px dashed #c4c4c4; left: 50%; transform: translateX(-50%); top: q(106);}
        &:after { top: q(186);}
        .swiper-container { flex: 1; height: q(300);
            .swiper-slide { line-height: q(66); font-size: q(26); color: #999; @include no-wrap(); padding: 0 q(22);}
            .swiper-slide-active { transform: scale(1.1); color: #333;}
        }
    }
    .close { color: #808080; font-size: q(28);}
    .ok { color: #3aa5ff; font-size: q(28);}
    .title { font-size: q(36); color: #666;}
    .close,
    .ok { @include extend-click();}
}

.borderBg { position: absolute; left: 0; right: 0; top: 0; bottom: 0; z-index: 2;}
.borderBg li { height: q(97.5); border-top: 1px dashed #ccc; transform: translateY(50%); }
.borderBg li.first-none,
.borderBg li.last-none { border: 0; }

// 蓝色问号样式，结构写在当前页面block里面
.iconQuery { position: absolute; display: block; width: q(34); height: q(34); top: 50%; right: 0; transform: translateY(-50%); background: url('../images/queryIcon.png') center/q(34) q(34) no-repeat;
    &:before { content: ''; position: absolute; top: -10px; left: -10px; right: -10px; bottom: -10px;}
}
