@import '../../lib/sass/px2rem';
.btn_main { display: block; height: q(80); line-height: q(80); border-radius: q(40); text-align: center; background: #3aa5ff; color: #fff; font-size: q(28);
    &:active { background: darken(#3aa5ff, 10%);}
}

.btn_sub { display: block; height: q(78); line-height: q(80); border: 1px solid #e5e5e5; border-radius: q(40); text-align: center; background: #fff; color: #808080; font-size: q(28);
    &:active { background: #f6f6f6;}
}
