@import '../../lib/sass/px2rem';
@import '../../lib/sass/base';

.promise { display: flex; flex-direction: row;
  .ifcheck { width: q(23); margin-right: q(18); padding-top: q(4);
      .ischeck { display: block; width: q(23); height: q(23); background: url('../images/unchecked.png') no-repeat; background-size: contain;}
      input[type="checkbox"]:checked +.ischeck { display: block; width: q(23); height: q(23); background: url('../images/checked.png') no-repeat; background-size: contain;}
  }
  .pro_detail { font-size: q(20); color: #666; line-height: q(36); position: relative;}
}
