.input_item { min-height: q(90); display: flex; align-items: center; justify-content: space-between; width: 100%; position: relative;
    .input_item_text { flex: 0 1 auto; font-size: q(26); color: #666; line-height: q(34);}
    .input_wraper { flex: 1; font-size: q(28); color: #666; line-height: q(34); text-align: right; padding-right: q(108); margin-left: q(40);
        .input_wraper_item { font-size: q(28); line-height: q(34); text-align: right; color: #666;}
    }
    .selectInput { display: block; font-size: q(28); line-height: q(36); outline: none; margin-left: auto; text-align: left; @include extend-click();}
    // 蓝色问号样式，结构写在当前页面block里面
    .iconQuery { position: absolute; display: block; width: q(34); height: q(34); top: 50%; right: 0; transform: translateY(-50%); background: url('../images/queryIcon.png') center/q(34) q(34) no-repeat;
        &:before { content: ''; position: absolute; top: -10px; left: -10px; right: -10px; bottom: -10px;}
    }
}
