@import '../../lib/sass/mixin';
.radio_item { display: flex; align-items: center; justify-content: space-between; width: 100%; min-height: q(90); overflow: hidden;
    .radio_item_text { flex: 0 1 50%; font-size: q(26); color: #666; line-height: q(34);}
    .radio_wraper { flex: 1; font-size: 0; text-align: right; margin-left: q(40);
        .radio_item_box { display: inline-block; vertical-align: middle; @include extend-click();
            .radio_item + .tip { display: inline-block; vertical-align: middle; width: q(26); height: q(26); background: url('../images/radio_unchecked.png') left/100% 100% no-repeat;}
            .radio_item:checked + .tip { background: #fff url('../images/radio_checked.png') left/100% 100%  no-repeat;}
            .text { display: inline-block; vertical-align: middle; font-size: q(28); color: #666; margin-left: q(10);}
            + .radio_item_box { margin-left: q(60);}
        }
    }
}

.radio_item2 { @extend .radio_item;
    .radio_wraper {
        .radio_item_box {
            .radio_item + .text { width: q(50); font-size: q(25); line-height: q(50); text-align: center; border: q(2) solid #e5e5e5; border-radius: q(8); background: #fff;}
            .radio_item:checked + .text { background: #3aa5ff; border: q(2) solid transparent; color: #fff;}
            + .radio_item_box { margin-left: q(18);}
        }
    }
}
