@import '../../lib/sass/px2rem';
@import '../../lib/sass/mixin';
@import '../../component/baseBtn/baseBtn';
@import '../../component/baseRadioItem/baseRadioItem';
@import '../../component/baseInputItem/baseInputItem';
@import '../../component/the-promise/the-promise';
@import '../../component/select/select';

.main { background-color: #f6f6f6; padding-bottom: q(38);
    .support_elder_title { font-size: q(32); line-height: q(36); color: #333; padding: q(28) q(36);}
    .form { background-color: #fff; border: 1px solid #e6e6e6; margin: 0 q(20);
        .form_item { border-bottom: 1px solid #f2f2f2; margin: 0 q(6); padding: 0 q(14);}
        .promiseBox { min-height: q(90); display: flex; align-items: center; padding: q(10) q(14);}
        .value_item .selectBox .selectInput { text-align: right;}
    }
    .form_date { display: flex; align-items: center; justify-content: space-between; min-height: q(90);
        .form_date_left { flex: 0 1 50%; font-size: q(26); color: #666; line-height: q(34);}
        .form_date_right { position: relative; flex: 1; text-align: right; padding-right: q(6);
            &:after { position: absolute; right: 0; top: 50%; margin-top: q(-19); width: q(39); height: q(39); content: ''; background: url('../images/calendar_icon.png') no-repeat; background-size: 100%;}
            .calendar_slect { font-size: q(28); height: q(34); color: #666; appearance: none; position: relative; background-color: transparent; z-index: 1;}
            .placeholder { position: absolute; right: q(108); top: 50%; transform: translateY(-50%); font-size: q(28); color: #c1c0c0;}
        }
    }
    .item_title { display: flex; justify-content: space-between; align-items: center;
        .closeItem { display: block; width: q(30); height: q(30); background: url('../images/closeBtn.png') center/q(30) q(30) no-repeat; @include extend-click();}
    }
    .addBtnBox { padding: q(40) q(22); margin: 0 q(20) q(20); background: #fff; border: 1px solid #e6e6e6; border-top: 0; margin-top: -1px; position: relative;
        &:before { content: ''; position: absolute; left: q(6); right: q(6); display: block; height: 1px; background-color: #f2f2f2; top: 0;}
    }
    .totalRadioBox,
    .mineMsg {
        margin-bottom: q(20);
        :last-child { border-bottom: 0;}
    }
    .shareForm {
        .shareTitle { line-height: q(90); font-size: q(30); color: #666;}
        .form_item { border-bottom: 0;
            + .form_item { border-top: 1px solid #f2f2f2; }
        }
        + .shareForm { margin-top: q(20);}
    }
}
.btnBox { padding: q(60) q(20); display: flex; justify-content: space-between;
    .btn_main,
    .btn_sub { flex: 1;}
    .saveBtn { margin-right: q(20);}
}
